import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HeaderColor } from '@ionic-native/header-color';
import { Vibration } from '@ionic-native/vibration';

import { MyApp } from './app.component';
import { Toast } from '../componentesIonic/toast';
import { Loading } from '../componentesIonic/loading';
import { Alertas } from '../componentesIonic/alertas';
import { NativeCamera } from '../services/nativeCamera';
import { Camera } from '@ionic-native/camera';
import { SocialSharingService } from '../services/socialSharing';
import { SocialSharing } from '@ionic-native/social-sharing';
import { TranslateModule, TranslateLoader, TranslateStaticLoader } from 'ng2-translate';
import { Http } from '@angular/http';
import { BD } from '../persistence/bd';
import { BDUtil } from '../persistence/bdUtil';
import { CategoryDao } from '../dao/categoryDao';

export function createTranslateLoader(http: Http) {
  return new TranslateStaticLoader(http, './assets/i18n', '.json');
}


@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/buttons/buttons.module.ngfactory#ButtonsPageModuleNgFactory', name: 'ButtonsPage', segment: 'buttons', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/camera/camera.module.ngfactory#CameraPageModuleNgFactory', name: 'CameraPage', segment: 'camera', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/config/config.module.ngfactory#ConfigPageModuleNgFactory', name: 'ConfigPage', segment: 'config', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/loading/loading.module.ngfactory#LoadingPageModuleNgFactory', name: 'LoadingPage', segment: 'loading', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/storage/storage.module.ngfactory#StoragePageModuleNgFactory', name: 'StoragePage', segment: 'storage', priority: 'low', defaultHistory: [] }
  ]
}),
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: (createTranslateLoader),
      deps: [Http]
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler }, SocialSharing,
    Toast, Loading, Alertas, HeaderColor, NativeCamera, Camera, SocialSharingService,
    Vibration, BD, BDUtil, CategoryDao
  ]
})
export class AppModule { }



